.login-container {
    width: 80%;
    max-width: 700px;
    margin: 100px auto; 
    padding: 40px; 
    border: 1px solid #ddd;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.login-container h1 {
    text-align: center;
    margin-bottom: 30px; 
}

.login-form .form-group {
    margin-bottom: 20px; 
}

.login-form label {
    display: block;
    margin: 20px auto 10px auto;
    font-weight: bold;
    margin-left: 125px;
}


.login-form input {
    width: 60%;
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 5px;
    margin: 0 auto 30px auto; 
    display: block; 

}

.login-button {
    width: 65%;
    padding: 15px; 
    border: none;
    border-radius: 5px;
    background-color: #1C3554;
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    display: block;
    margin: 40px auto;
}

.login-button:hover {
    background-color: #0c1723;
}

.forgot-password-link {
    display: block;
    text-align: center;
    margin-top: 20px;
    color: #1C3554;
    text-decoration: none;
    font-size: 14px;
}

.forgot-password-link:hover {
    text-decoration: underline;
}